import { useState } from "react";
import { RiCloseCircleLine } from "react-icons/ri";
import { TiEdit } from "react-icons/ti";
export default function Todo({
  updateTodo,
  todo,
  text,
  todos,
  id,
  remove,
  completeTodo
}) {
  const [input, setInput] = useState("");
  const [newTodo, setNewTodo] = useState({
    id: null,
    text: ""
  });

  const onSubmit = (e, value) => {
    e.preventDefault();
    console.log(id, value);
    updateTodo(id, value);
    setInput("");
    setNewTodo({
      id: null,
      text: ""
    });
  };

  const handleChange = (e) => {
    setInput(e.target.value);
  };

  if (newTodo.id) {
    return (
      <>
        <form
          onSubmit={(e) => {
            onSubmit(e, input);
          }}
          className="todo-row edit"
        >
          <input
            value={input}
            onChange={handleChange}
            placeholder={text}
            name="text"
            className="todo-input edit"
          />
          <button
            onSubmit={(e) => {
              onSubmit(e, input);
            }}
            className="todo-button edit"
          >
            Edit
          </button>
        </form>
      </>
    );
  }
  return (
    <div
      className={todo.isComplete ? "todo-row complete" : "todo-row"}
      onDoubleClick={() => completeTodo(id)}
    >
      <p> {text} </p>
      <div className="icons">
        <RiCloseCircleLine onClick={() => remove(id)} className="delete-icon" />
        <TiEdit
          onClick={() => setNewTodo({ id: id, value: text })}
          className="edit-icon"
        />
      </div>
    </div>
  );
}
